.form {
	min-width: 100%;
	position: relative;
	.block {
		&__title {
			padding: 20px 50px;
			@include r($sm) {
				padding: 15px 40px; }

			h3 {
				font-weight: 600; } } }
	&__in {
		padding: 20px 50px;


		@include r($sm) {
			padding: 15px 40px; } }
	&__success {
		background-color: transparentize($violet, .2);
		@include coverdiv;
		display: none;
		p {
			@include vcenter;
			width: 90%;
			font-size: 18px;
			color: $yellow; } }
	.btn {
		display: block;
		margin: 0 auto; } }
.input {
	position: relative;
	font-size: 14px;
	margin-bottom: 15px;
	input {
		width: 100%;
		height: 44px;
		border: 1px solid $gray-bg;
		border-radius: 2px;
		padding: 2px 20px 0;
		-webkit-box-shadow: inset 0 0 0 50px #fff {
    	-webkit-text-fill-color: #000; }
		&:focus {
			&::placeholder {
				color: transparent; } } }
	label {
		background-color: #fff;
		display: inline-block;
		padding: 0 5px;
		position: absolute;
		bottom: -6px;
		right: 7px;
		font-size: 12px;
		color: #E34848;
		@include r($sm) {
			font-size: 8px;
			bottom: -4px; } }
	textarea {
		width: 100%;
		height: 120px;
		padding: 14px 20px;
		border: 1px solid $gray-bg;
		border-radius: 2px;
		resize: none;
		&+label {
			bottom: -1px;
			@include r($sm) {
				bottom: 1px; } }
		&::placeholder {
			@include u; }
		&:focus {
			&::placeholder {
				color: transparent; } } } }
